    <scroll-top v-show="isGoShow" @goToTop="mainSetGo"></scroll-top>
</div>
</template>
<script>
  import topNav from '../common/topNav.vue';
  import scrollTop from '../common/scrollTop.vue';
  import loadingBg from '../common/refresh.vue';
  import addData from '../../common/js/scrollAddData.js';
  export default {
    data() {
      return {
        isActive: false, // 小按钮
        isUl: false, // 栏目下拉
        isGoShow: false, // 回到顶部
        isLoading: false,
        articals: [
          {},
          {},
          {}
        ],
        articalItems: [
          {"title": "瓦讷站", "url": "/mainPage1"},
          {"title": "瓦讷站", "url": "/mainPage2"},
          {"title": "瓦讷站", "url": "/mainPage3"}
        ]
      }
    },
    methods: {
      mainUrl(item , index) {
        var index = index + 1;
        this.$router.push({ path: '/mainPage' + index, query: { plan: 'private' }});
      },
      mainSetGo(data) {
        this.isGoShow = data;
      }
    },
    created() {
    },
    mounted() {
      var self = this;
      addData('.main-container-page', '.main-container', self);
    },
    components: {
      'scroll-top': scrollTop,
      'top-nav': topNav,
      'loading-bg': loadingBg
    }
  }
</script>
<style scoped>
  .main-container {
    position: relative;
    top: 0rem;
    width: 100%;
    box-sizing: border-box;
  }

  .main-contain {
    position: relative;
    top: 2rem;
    width: 100%;
    box-sizing: border-box;
  }

  .main-page {
    position: relative;
    padding: 0.5rem;
    margin-top: 1rem;
    box-sizing: border-box;
    box-shadow: 0 0 0.133333rem rgba(202,203,203,0.7);
  }

  .main-page-container {
    width: 80vw;
    text-indent: 0.5rem;
    font-size: 14px;
    text-align: left;
    box-sizing: border-box;
    padding: 0.5rem 0;

    white-space:pre-wrap;
    word-wrap:break-word;
    word-break:break-all;
  }

  .main-page-info {
    position: relative;
    top: 0rem;
    margin-bottom: 1.066667rem;
  }

  .main-container-page {
    position: absolute;
    top: 0.5rem;
    padding-bottom: 2.5rem;
    width: 9rem;
    left: 0.5rem;
    box-sizing: border-box;
  }

  .main-page-text {
    position: absolute;
    top: -1rem;
    width: 100%;
    text-align: center;
  }

  p {
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 0.506667rem;
    max-height: 1.013333rem;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    white-space: normal;
  }
</style>
